kaboom.global();

import objects from './lib/objects';
import background from './entities/background';
import grass from './entities/grass';
import diamond from './entities/diamond';
import createPlayer from './entities/player';
import openDoor from './entities/openDoor';
import lockedDoor from './entities/lockedDoor';

loadSound('coin', 'coin.ogg');
loadSound('win', 'achieved.ogg');

loadSprite('player', 'player.png', {
    sliceX: 4,
    sliceY: 2,
    anims: {
        idle: [0],
        walk: [2, 3],
        jump: [1],
        win: [7]
    }
});

for(let obj of objects) {
    loadSprite(obj, `${obj}.png`);
}

init({
    width: 640,
    height: 480,
    canvas: document.querySelector('#game')
});

scene('main', () => {

    const DIAMOND_COUNT = width() / 128;

    layers(['bg', 'sprites', 'ui'], 'sprites');

    add(background());
    const player = add(createPlayer());

    for(let i = 0; i < width() / 64; i++) {
        let block = add(grass());
        block.pos.y = height() - 64;
        block.pos.x = i * 64;
    }

    for(let i = 0; i < DIAMOND_COUNT; i++) {
        let d = add(diamond());
        d.pos.y = height() - 200;
        d.pos.x = i * 128 + 32;
    }

    add(lockedDoor());

    player.action(() => {
        if(player.diamondsCollected === DIAMOND_COUNT) {
            add(openDoor());
            readd(player);
            player.diamondsCollected = 0;
        }
    });


    player.on('grounded', () => {
        player.frame = 0;
    });

     keyDown('a', () => {
        if(player.pos.x > player.area.p2.x / 2) {
            player.move(-player.speed, 0);
            player.flipX(-1);
            if(player.grounded() && player.curAnim() !== 'walk') {
                player.play('walk');
            }
        }
    });
    keyDown('d', () => {
        if(player.pos.x < width() - player.area.p2.x / 2) {
            player.flipX(1);
            player.move(player.speed, 0);
            if(player.grounded() && player.curAnim() !== 'walk') {
                player.play('walk');
            }
        }
    });
    keyRelease(['a', 'd'], () => {
        if(player.grounded()) {
            player.play('idle');
            player.stop();
        }
    });
    keyPress('space', () => {
        if(player.grounded()) {
            player.jump();
            if(player.curAnim() !== 'jump') {
                player.play('jump');
                player.stop();
            }
        }
    });

    overlaps('player', 'diamond', (p, d) => {
        p.diamondsCollected += 1;
        destroy(d);
        play('coin', { loop: false, speed: 1.25 });
    });

    overlaps('player', 'openDoor', (p, d) => {
        play('win');
        wait(1.5, () => {
            go('gameover');
        });
    });

});

scene('gameover', () => {

    const winText = add([
        text('You win!'),
        pos(0, 0)
    ]);

    winText.pos.x = width() / 2 - winText.area.p2.x / 2;
    winText.pos.y = height() / 2;

    const hitEnter = add([
        text('Press Enter'),
        pos(0, 0)
    ]);

    hitEnter.pos.x = width() / 2 - hitEnter.area.p2.x / 2;
    hitEnter.pos.y = height() / 2 + winText.area.p2.y;

    keyPress('enter', () => {
        go('main');
    });
});

start('main');